  font-family: "Roboto";
  src: url("../fonts/Roboto-Italic.ttf") format("ttf");
  font-weight: 400;
  font-style: normal;
}
/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  background-color: #FFFFFF;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.8rem;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

.contenedor {
  max-width: 80rem;
  margin: 0 auto;
  padding: 2.8rem;
  border-bottom: 1px solid #101010;
}

p {
  color: #101010;
}

li {
  list-style: none;
  line-height: 1.5;
}

h1 {
  font-size: 5rem;
}

h3 {
  font-size: 2rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.3rem;
  text-align: center;
}

p, a {
  font-size: 1.6rem;
}

.text-center {
  text-align: center;
}

.mayus {
  text-transform: uppercase;
}

.small {
  font-size: 1.4rem;
}

.flex-horizontal {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 2rem;
}

.flex-vertical {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 3rem;
}

.margin2rem {
  margin: 2rem;
}

header {
  display: flex;
  flex-direction: column;
  padding: 3.5rem 2.8rem 3.6rem 2.8rem;
  justify-content: center;
  align-items: center;
  background-color: #06073d;
  gap: 0.4rem;
}

h1 {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  letter-spacing: 0.75rem;
  display: flex;
  flex-direction: column;
}

.h1-grande {
  color: #e5da44;
}

.h1-small {
  color: #FFFFFF;
  font-size: 2.5rem;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 0.75rem;
  margin: 0;
}

nav {
  display: flex;
  width: 100%;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1024px) {
  nav {
    width: auto;
  }
}
nav .botones--nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
@media (min-width: 1024px) {
  nav .botones--nav {
    width: auto;
  }
}
@media (min-width: 1024px) {
  nav {
    flex-direction: row;
  }
}
@media (min-width: 1024px) {
  nav {
    align-items: center;
  }
}
nav ul {
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 1024px) {
  nav ul {
    flex-direction: row;
    gap: 0rem;
  }
}
@media (min-width: 1024px) {
  nav ul {
    gap: 1rem;
  }
}
nav li {
  transition-property: background-color;
  transition-duration: 0.3s;
  border-radius: 0.2rem;
  display: flex;
  justify-content: center;
}
nav li a {
  color: #e5da44;
  padding: 2.5rem 1rem;
}
@media (min-width: 1024px) {
  nav li a {
    padding: 2.5rem 2rem;
  }
}
nav li:hover {
  background-color: #01020f;
}

.navegacion__boton {
  cursor: pointer;
  display: flex;
  margin: 0;
  justify-content: center;
}
@media (min-width: 1024px) {
  .navegacion__boton {
    margin: 2rem 0 2rem 0;
  }
}
@media (min-width: 768px) {
  .navegacion__boton {
    justify-content: end;
  }
}
@media (min-width: 1024px) {
  .navegacion__boton {
    display: none;
  }
}
.navegacion__boton:hover {
  background-color: #01020f;
  cursor: pointer;
}

.navegacion__boton--mostrar {
  display: block;
  height: 5rem;
  width: 5rem;
}

.navegacion__boton--cerrar {
  display: none;
  height: 5rem;
  width: 5rem;
}

.navegacion__enlaces {
  display: none;
}
@media (min-width: 1024px) {
  .navegacion__enlaces {
    display: flex;
  }
}

.redes {
  margin: 0;
  display: flex;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .redes {
    margin: 2rem 0 2rem 0;
  }
}
.redes a {
  height: 4.8rem;
  width: 4.8rem;
  -webkit-box-shadow: 0px 0px 5px 2px #7e7712;
  -moz-box-shadow: 0px 0px 5px 2px #7e7712;
  box-shadow: 0px 0px 5px 2px #7e7712;
}
.redes a:hover {
  background-color: #01020f;
  cursor: pointer;
}

.header h2 {
  list-style: none;
  padding: 1rem 2rem 1rem 2rem;
  border-top: 0.5px solid #7e7712;
  border-bottom: 0.5px solid #7e7712;
  margin-top: 1rem;
  text-align: center;
  display: grid;
  grid-template-rows: repeat(4, 1fr);
  gap: 1rem;
}
@media (min-width: 768px) {
  .header h2 {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
}
.header .pueblo {
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 1.2rem;
  color: #e5da44;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .header .pueblo {
    grid-column: 1/4;
  }
}
.header .descripcion {
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 1.2rem;
  color: #e5da44;
  margin: 0;
  padding: 0;
}

footer {
  display: flex;
  flex-direction: column;
  background-color: #06073d;
  padding: 2.8rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  footer {
    flex-direction: row;
  }
}
footer p, footer a {
  color: #FFFFFF;
  padding: 2rem 0;
  margin: 0 auto;
}

.boton {
  background-color: #06073d;
  padding: 2rem;
  color: #e5da44;
  font-size: 1.6rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  font-style: normal;
  line-height: normal;
  letter-spacing: 0.24rem;
  text-transform: uppercase;
}
.boton:hover {
  background-color: #01020f;
  cursor: pointer;
}

.botonera {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .botonera {
    flex-direction: row;
  }
}

form fieldset {
  padding: 0;
  border: none;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 0.9rem;
  align-items: start;
}
form .entrada {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
form .entrada input {
  background-color: #DCD5D5;
  border: none;
  padding: 1rem;
}

.partes {
  display: flex;
  flex-direction: column;
  gap: 0.9rem;
  align-items: center;
}
.partes form {
  width: 100%;
}
@media (min-width: 1024px) {
  .partes {
    width: 70rem;
  }
}
.partes fieldset {
  display: flex;
  align-items: start;
  width: 100%;
}
.partes .entrada--checkbox {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}
.partes .entrada--checkbox span {
  font-weight: 700;
}
@media (min-width: 1024px) {
  .partes .grid-2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.formulario {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 40rem;
  border-bottom: none;
}
@media (min-width: 1024px) {
  .formulario {
    max-width: 70rem;
  }
}
@media (min-width: 1024px) {
  .formulario .grid-2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.contacto {
  max-width: 40rem;
}
.contacto fieldset {
  width: 100%;
}
@media (min-width: 1024px) {
  .contacto {
    max-width: 70rem;
  }
}
.contacto .entrada {
  width: 100%;
}

input[type=file] {
  display: none;
}

.archivo {
  font-size: 14px;
  text-transform: none;
  padding: 0.5rem;
  width: 100%;
  background-color: #DCD5D5;
  color: #06073d;
}
.archivo svg {
  fill: #06073d;
  stroke: #06073d;
}
.archivo:hover {
  color: #e5da44;
}
.archivo:hover svg {
  fill: #e5da44;
  stroke: #e5da44;
}

.submit {
  display: flex;
  margin: 0 auto;
  justify-content: center;
}
.submit .boton {
  width: 100%;
  max-width: 40rem;
}
@media (min-width: 1024px) {
  .submit .boton {
    max-width: 70rem;
  }
}

.selector {
  position: relative;
  width: 100%;
  max-width: 50rem;
  margin: 0 auto;
}

.selector--picture {
  z-index: 0;
}

.selector--svg {
  align-items: start;
  display: flex;
  width: 100%;
  position: absolute;
  top: 49%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
}

.selector--pieza:hover {
  fill: #01020f;
  fill-opacity: 30%;
  cursor: pointer;
}

.selector--marcada {
  fill: black;
  fill-opacity: 40%;
}

.precio {
  background-color: #06073d;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.precio p {
  color: #e5da44;
  margin: 0;
}
.precio p:last-child {
  margin-top: 1rem;
}

.precio-valor {
  font-size: 5rem;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  letter-spacing: 0.75rem;
  color: #e5da44;
}

.admin-imagenes div {
  padding: 0.5rem;
  border: 1px solid #625959;
}
@media (min-width: 768px) {
  .admin-imagenes {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .admin-imagenes {
    grid-template-columns: repeat(3, 1fr);
  }
}
.admin-imagenes .boton {
  width: 100%;
}

.alertas {
  margin: 0 auto;
}
.alertas p {
  padding: 1rem 2rem;
}

.error {
  background-color: rgb(169, 0, 0);
  color: #FFFFFF;
}

.exito {
  background-color: #02db02;
  color: #101010;
}

.ubicacion--texto {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.ubicacion--texto a {
  color: #06073d;
}
.ubicacion--texto .ubicacion--dato {
  text-align: right;
  gap: 2rem;
  display: flex;
  justify-content: space-between;
}

@media (min-width: 1024px) {
  .contenedor-hero {
    display: flex;
    justify-content: center;
  }
  .contenedor-hero img {
    margin: 0 auto;
    max-height: 60rem;
