.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 12;
  height: 100%;
  width: 100;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
